<template>
  <div id="aboutUs">
    <van-nav-bar>
      <div slot="title">
        关于我们
      </div>
      <van-icon name="arrow-left" slot="left" color="black" @click="$router.go(-1)"/>
    </van-nav-bar>
    <van-cell-group>
      <van-cell is-link v-for="(item,index) in lists" :value="item.title" :key="index" @click="$router.push({path:'/particulars', query:{contents:item.content,titles:item.title}})"/>
    </van-cell-group>
  </div>
</template>

<script>
  export default {
    name: "aboutUs",
    data() {
      return {
        lists:[],
      }
    },
    created (){
      this.classify()
    },
    methods:{
      classify(){
        let self = this
        this.$POST('/article/article').then(res =>{
          console.log(res);
          if(res.code===200 ){
            self.lists = res.data
          }else {
            this.$toast(res.msg);
          }
        }).catch(res=>{
          this.$toast('连接服务器失败!');
          console.log(res);
        })
      },
    },
  }
</script>

<style scoped>
  #container img{
    width: 100%;
    height: 100%;
  }
</style>



